.splash-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 100vh;
    background-color: #1e1e2f;
}

.app-name {
    font-size: 3rem;
    color: #f5f5f5;
    animation: fadeIn 2s;
}

.get-started {
    padding: 1rem 2rem;
    background-color: #0077ff;
    color: white;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    transition: background 0.3s;
}

.get-started:hover {
    background-color: #005bb5;
}

@keyframes fadeIn {
    from {
        opacity: 0;
    }
    to {
        opacity: 1;
    }
}

